.list {
  display: flex;
  justify-content: center;
  flex-direction: column;

  padding: 20px;
  border-radius: 4px;
}

.item {
  background-color: #fff;

  padding: 10px;
  border-radius: 4px;
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;

  background-color: rgb(220, 255, 210);
  border: 1px solid rgb(0, 0, 0);
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 5px;
  display: flex;
}

.contact {
  margin-right: 30px;
}
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100px;

  padding: 2px 32px;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border-style: none;
  cursor: pointer;
  font-weight: bold;

  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;
  color: #ffffff;
  background-color: #166a1e;
}
